  <!---->
  <template>
  <div class="cms-form-layout loading-top product-main-warpper" v-loading='loading' :element-loading-text="loadingTitle">
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent 
    style="margin-bottom: 48px;">
      <el-form-item label="" prop="coreModel.salePrice" ref="coreModel_salePrice">
        <el-input v-model="formData.coreModel.salePrice" maxlength="8"></el-input>
        <span class="gray">0.01~99999999SKUSKU
        </span>
      </el-form-item>
      <el-form-item label="" prop="coreModel.marketPrice" ref="product_name">
        <el-input v-model="formData.coreModel.marketPrice" maxlength="8">
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="coreModel.costPrice">
        <el-input v-model="formData.coreModel.costPrice" maxlength="8">
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="coreModel.inventory" ref="coreModel_inventory">
        <el-input v-model="formData.coreModel.inventory" maxlength="8" ></el-input>
        <span class="gray">1~99999999SKU SKU
        </span>
      </el-form-item>
      <el-form-item label="" prop="coreModel.warningValue">
        <el-input v-model="formData.coreModel.warningValue" maxlength="8"></el-input>
        <span class="gray">SKU SKU
        </span>
      </el-form-item>
      <el-form-item label="" prop="coreModel.hasSku">
        <div>
          <el-checkbox v-model="formData.coreModel.hasSku" @change="setCoreModel"></el-checkbox>
          <span class="gray">SKU</span>
        </div>
        <!--  -->
        <el-checkbox-group v-model="checkSaleStandardIds" v-if="formData.coreModel.hasSku">
          <section class="standard-group" v-for="(group,index) in saleStandardInfos" :key="group.id">
            <div class="standard-group-header">
              <div class="fl">
                <span class="standard-group-name"><i v-if="group.required" class="goods-astrick">*</i>{{group.name}}</span>
                <span class="standard-group-desc" v-if="group.valueType==4">(  )</span>
                <!--  -->
                <span class="el-icon-delete del" v-if="group.attrType===2" @click="deleteCustomStandard(group,index)"></span>
              </div>
              <div class="fr">
                <label class="standard-group-desc"></label>
                <el-input style="width:110px" v-model="customStandardValue['value'+index]" maxlength="30"></el-input>
                <cms-button size='small' @click='addCustomStandard(index)' style="margin-left:10px;"
                :disabled="!group.custom"></cms-button>
              </div>
            </div>
            <div class="standard-group-body">
              <!--  -->
              <ul v-if="group.valueType===4" class="clearfix">
                <li v-for="(item,index2) in group.attrValueList" :key="item.id" class="type-img-items">
                  <cms-picture-zone :preview='false' :index='index2' :params="{parentIndex:index}" class="product-img-box" 
                   @change="getStandardImg" v-model="item.valueResourceId">
                    <div class="product-img-warpper">
                      <img v-if="item.valueResourceId&&item.valueResourceId!==''" :src="$cms.filterUrl(item.url)" alt="">
                      <span v-else class="img-plus el-icon-plus"></span>
                    </div>
                  </cms-picture-zone>
                  <div class="img-txt">
                    <el-checkbox :label="item.attrCode" class="check-box" v-model="item.isChecked" 
                      @change="setProperties" :disabled="!item.valueResourceId">
                      <span class="type-value">{{item.attrValue}}</span>
                      <div class="el-icon-delete del" 
                        @click.stop="deleteCustomStandardValue(item,index,index2)" v-if="item.attrType==2">
                      </div>
                    </el-checkbox>
                  </div>
                </li>
              </ul>
              <ul v-else  class="clearfix">
                <li v-for="(item,index2) in group.attrValueList" :key="item.attrCode" class="type-checkbox-items">
                  <el-checkbox :label="item.attrCode"  @change="setProperties">
                    <span class="type-value">{{item.attrValue}}</span>
                    <div class="el-icon-delete del" @click.stop="deleteCustomStandardValue(item,index,index2)" v-if="item.attrType===2"></div>
                  </el-checkbox>
                </li>
              </ul>
            </div>
          </section>
          <div style="margin:20px 0 0 0">
            <cms-button icon='add' @click="popoverVisible=true" size="small" v-if="canCustomAttr"></cms-button>
          </div>
          <!--  -->
          <el-dialog title="" top="35vh" :visible='popoverVisible' width="640px"  :before-close="handleClose">
            <div class="dialog-form">
              <el-form :model="popoverForm" ref='dialogForm'>
                <el-form-item label="" prop="name" label-width="120px" :rules="
                [{ required: true, message: '', trigger: 'blur' }]">
                  <el-input v-model="popoverForm.name" maxlength="50"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px" prop="attrValues" :rules="
                [{ required: true, message: '', trigger: 'blur' }]">
                  <el-input v-model="popoverForm.attrValues" type='textarea' 
                  :rows="4" placeholder="" maxlength="80"></el-input>
                </el-form-item>
                <div class="center">
                  <cms-button @click="addCustomStandardItem" style="margin-right:10px;"></cms-button>
                  <cms-button @click="clearCustomStandardItem" type='info'></cms-button>
                </div>
              </el-form>
            </div>
          </el-dialog>
        </el-checkbox-group>
      </el-form-item>
      <!-- sku -->
      <el-form-item label="sku" prop="name" v-if='formData.coreModel.hasSku&&skus.length>0'>
        <table class="sku-table">
          <tr>
            <th v-for="(item,index) in properties" :key="index" v-if="item.attrValueList.length>0" rowspan='2'>
              {{item.name}}
            </th>
            <th><i class="goods-astrick">*</i></th>
            <th></th>
            <th></th>
            <th><i class="goods-astrick">*</i></th>
            <th></th>
            <th rowspan='2' width='60'></th>
          </tr>
          <tr>
            <td>
              <el-input class="table-input" :min="0" v-model="batch.salesPrice" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('salesPrice',1)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" :min="0" v-model="batch.marketPrice" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('marketPrice',1)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" :min="0" v-model="batch.costPrice" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('costPrice',1)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" :min="0" v-model="batch.inventory" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('inventory',2)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" :min="0" v-model="batch.warning" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('warning',3)"></i>
                </template>
              </el-input>
            </td>
          </tr>
          <tr v-for="(item,index2) in skus" :key="index2">
            <td v-for="(name,index3) in item.name" :key="index3" class="min-w-90">
              {{name}}
            </td>
            <td>
              <el-input v-model="item.salesPrice" :min="0" maxlength="8"></el-input>
            </td>
            <td>
              <el-input v-model="item.marketPrice" :min="0" maxlength="8"></el-input>
            </td>
            <td>
              <el-input v-model="item.costPrice" :min="0" maxlength="8"></el-input>
            </td>
            <td>
              <el-input v-model="item.inventory" :min="0" maxlength="8"></el-input>
            </td>
            <td>
              <el-input v-model="item.warning"  :min="0" maxlength="8"></el-input>
            </td>
            <td>
              <i class="el-icon-delete del" @click="deleteSkus(item,index2)"></i>
            </td>
          </tr>
        </table>
        <span class="gray">SKU</span>
      </el-form-item>
      <!--  -->
      <el-form-item label="" prop="name" v-if="productStandardInfos.length>0">
        <section class="standard-group" v-for="(group,index) in productStandardInfos" :key="group.id" 
         :style="index===0?{marginTop:0}:''">
          <div class="standard-group-header">
            <div class="fl">
              <span class="standard-group-name">{{group.attrGroupName}}</span>
            </div>
            <div class="fr">
              <label class="standard-group-desc"></label>
              <el-input style="width:110px" v-model="customAttributeValue['value'+index]" maxlength="30"></el-input>
              <cms-button size='small' style="margin-left:10px;" @click="addCustomAttribute(index)"></cms-button>
            </div>
          </div>
          <div class="standard-group-body" style="padding:20px;">
            <!--  -->
            <div class="clearfix" :class="[standard.valueType==1?'w-50':'w-100']" 
              v-for='(standard,index2) in group.attrs' :key="standard.id">
              <label class="fl  attribute-name">
                <i class="goods-astrick" v-show="isAttrRequired(standard)">*</i>
                {{standard.name}}
              </label>
              <!--  -->
              <ul class="fl" v-if="standard.valueType===3 && standard.attrValueList.length > 0">
                <li v-for="(item) in standard.attrValueList" :key="item.id" class="type-checkbox-items">
                  <el-checkbox :label="item.id" @change="setProductInfos(item,standard,1)">
                    <span class="type-value">{{item.attrValue}}</span>
                  </el-checkbox>
                </li>
              </ul>
              <!--  -->
              <ul class="fl" v-if="standard.valueType===2 && standard.attrValueList.length > 0">
                <!-- 0.0 -->
                <el-radio-group v-model="attributeGroups[standard.id+'s']">
                  <li v-for="(item) in standard.attrValueList" :key="item.id" class="type-radio-items">
                    <el-radio :label="item.id" @change="setProductInfos(item,standard,2)">
                      <span class="type-value">{{item.attrValue}}</span>
                    </el-radio>
                  </li>
                </el-radio-group>
              </ul>
              <!--  -->
              <div v-if="standard.valueType===1">
                <el-input @blur='setProductInfos($event,standard,3,standard.attrVc)' maxlength="50"
                  v-model="standard.attrVal"
                  style="width:220px;position: relative;top:0px;">
                </el-input><br>
                <div class="el-icon-delete del" @click="deleteCustomAttribute(standard,index,index2)" v-if="standard.attrType===2"></div>
              </div>
            </div>
          </div>
        </section>
      </el-form-item>
    </el-form>
    <div class="product-footer" :class="[fixed?'fixed-footer':'']">
      <cms-button @click="prevStep" style="background:#f7f7f7;color:#444"></cms-button>
      <!-- <cms-button v-color>,</cms-button> -->
      <cms-button v-color @click="save" style="background:#efae00"></cms-button>
    </div>
  </div>
</template>
  <script>
import rules from '@/rules'
import {
  productStandardValue,
  productStandardDelete
} from '@/api/product/type.js'
import {
  productSave,
  customStandard,
  customAttribute,
  customStandardDelete,
  customStandardItem
} from '@/api/product/product.js'
import { CreateSkus } from '@/utils/descartes'
var defaultData = {
  coreModel: {
    salePrice: '',
    marketPrice: 0.0,
    costPrice: 0.0,
    inventory: 10000,
    warningValue: 10,
    hasSku: false
  },
  skuTypeAttrs: [],
  skus: [],
  proTypeAttrs: []
}
export default {
  data() {
    var validatePrice = (rule, value, callback) => { // 
      if (parseFloat(value) <= 0) {
        callback(new Error('0'))
      } else {
        callback()
      }
    }
    var validateNum = (rule, value, callback) => { // 
      if (value === '') {
        callback(new Error(''))
      } else if (this.formData.coreModel.warningValue === '' || this.formData.coreModel.warningValue === 0) {
        callback()// 0
      } else if (value < this.formData.coreModel.warningValue) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateWarn = (rule, value, callback) => { // 
      const reg = /^[0-9]{0,8}$/
      if (value === '') {
        callback()
      } else if (!reg.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      loading: false,
      status: false,
      fixed: true,
      popoverVisible: false,
      popoverForm: {
        name: '',
        attrValues: ''
      },
      loadingTitle: ',',
      formData: Object.assign({}, defaultData),
      batch: {
        salesPrice: '',
        costPrice: '',
        inventory: '',
        marketPrice: '',
        warning: ''
      },
      rules: {
        'coreModel.salePrice': [rules.required(), rules.float(), rules.decimal(), rules.custom(validatePrice)],
        'coreModel.marketPrice': [rules.float(), rules.decimal()],
        'coreModel.costPrice': [rules.float(), rules.decimal()],
        'coreModel.inventory': [rules.custom(validateNum), rules.required(), rules.number()],
        'coreModel.warningValue': [rules.custom(validateWarn)],
        'coreModel.hasSku': [rules.required()]
      },
      customStandardValue: {}, // 
      customAttributeValue: {}, // 
      checkSaleStandardIds: [], // id
      checkProductStandardIds: [], // id
      properties: [], // 
      attributes: [], // 
      saleStandardInfos: [], // 
      productStandardInfos: [], // 
      skus: [], // sku
      attributeGroups: {},
      productTypeId: 0, // id
      storeId: 0, // id
      canCustomAttr: ''// 
    }
  },
  computed: {
    allCheckedLength: function() {
      // sku
      var len = 0
      this.properties.forEach(v => {
        len = len + v.attrValueList.length
      })
      return len
    }
  },
  methods: {
    getProductInfo(id) {
      this.productTypeId = this.$store.state.product.productTypeId // id id 
      this.storeId = this.$store.state.product.storeId
      productStandardValue({
        productTypeId: this.productTypeId
      }).then(res => {
        // 
        if (res.data && res.data.saleAttrs.length > 0) {
          this.saleStandardInfos = res.data.saleAttrs
          this.setRadioProperties(res.data.saleAttrs)// id
          const arr = []
          this.saleStandardInfos.map(v => {
          // 
            arr.push({
              valueType: v.valueType,
              attrGroupName: v.attrGroupName,
              name: v.name,
              id: v.id,
              attrValueList: []
            })
          })
          this.properties = arr
          this.productStandardInfos = res.data.nsaleAttrs
          res.data.nsaleAttrs.forEach(v => {
            v.attrs.forEach(e => {
              if (e.valueType === 1 && !e.attrVal) {
                var arr = []
                var arr2 = []
                e.attrValueList.forEach(f => {
                  arr.push(f.attrValue)
                  arr2.push(f.attrCode)
                })
                e.attrVc = arr2.join(',')
                e.attrVal = arr.join(',')
              }
            })
          })
          this.canCustomAttr = res.data.productType.custom// 
        }
        this.loading = false
      })
    },
    setRadioProperties(data) {
      data.forEach(v => {
        if (v.valueType === 4) { // 
          v.attrValueList.forEach(vv => {
            vv.valueResourceId = ''
          })
        }
      })
    },
    setProperties(item) {
      // 
      const ids = this.checkSaleStandardIds
      this.saleStandardInfos.forEach((v, i) => {
        const tmp = []
        v.attrValueList.forEach((vv, j) => {
          if (ids.indexOf(vv.attrCode) > -1) {
            tmp.push(vv)
          }
        })
        this.properties[i].attrValueList = tmp
      })
    },
    setProductInfos(item, parentItem, type, text) {
      //  type=1 2 3 
      let flag = true // 
      const obj = {
        groupName: parentItem.attrGroupName,
        attrName: parentItem.name,
        attrVc: item.attrCode,
        proTypeAttrId: parentItem.id
      }
      if (type === 3) {
        obj.attrVc = text
      } else {
        obj.attrVc = item.attrCode
      }

      if (this.attributes.length === 0) {
        this.attributes.push(obj)
      } else {
        for (const v of this.attributes) {
          if (v.proTypeAttrId === obj.proTypeAttrId) {
            const codeArr = v.attrVc.split(',')
            if (type === 1) {
              // ,
              const indexOf = codeArr.indexOf(obj.attrVc)
              if (indexOf > -1) {
                // 
                codeArr.splice(indexOf, 1)
              } else {
                // 
                codeArr.push(obj.attrVc)
              }
              v.attrVc = codeArr.join(',')
            } else {
              // 
              v.attrVc = obj.attrVc
            }
            flag = false
            break
          }
        }
        if (flag) {
          this.attributes.push(obj)
        }
      }
    },
    deleteSkus(item, index) { // skus
      this.skus.splice(index, 1)
      this.sysDeletedSkus()
    },
    sysDeletedSkus() { // sku
      let skuAttrCodes = []// skuattrCode
      this.skus.forEach(val => {
        skuAttrCodes = skuAttrCodes.concat(val.attrCodes)
      })
      this.properties.forEach((v, i) => {
        v.attrValueList.forEach((vv, j) => { // 
          if (!skuAttrCodes.includes(vv.attrCode)) { // skus
            const checkdIndex = this.checkSaleStandardIds.indexOf(vv.attrCode)
            this.checkSaleStandardIds.splice(checkdIndex, 1)
            v.attrValueList.splice(j, 1)
          }
        })
      })
    },
    setCoreModel(status) { // sku()()
      if (status) { // 
        let [salePrice, marketPrice, costPrice, inventory, warning] = [[], [], [], [], [], []]// 
        if (this.formData.skus.length > 0) {
          this.formData.skus.forEach((v, i, arr) => { // 
            salePrice.push(v.salesPrice)
            marketPrice.push(v.marketPrice)
            costPrice.push(v.costPrice)
            inventory.push(v.inventory)
            warning.push(v.warning)
          })
          // 
          salePrice = salePrice.sort((a, b) => { return a - b })
          marketPrice = marketPrice.sort((a, b) => { return a - b })
          costPrice = costPrice.sort((a, b) => { return a - b })
          inventory = inventory.reduce((a, b) => { return parseInt(a) + parseInt(b) })
          warning = warning.reduce((a, b) => { return parseInt(a) + parseInt(b) })
          // 
          this.formData.coreModel.salePrice = salePrice[0]
          this.formData.coreModel.marketPrice = marketPrice[0]
          this.formData.coreModel.costPrice = costPrice[0]
          this.formData.coreModel.inventory = inventory
          this.formData.coreModel.warningValue = warning
        }
      }
    },
    createdSkus() {
      // sku
      const data = []
      this.properties.forEach(v => {
        // 
        data.push(v.attrValueList)
      })
      this.skus = CreateSkus(data)
    },
    batchPrice(name, type) { // 
      const reg1 = /^\d{0,8}\.{0,1}(\d{1,2})?$/// 
      const reg2 = /^[1-9]{1}[0-9]*$/// 
      const reg3 = /^[0-9]+$/// 
      this.skus.forEach(v => {
        if (type === 1 && reg1.test(this.batch[name]) && this.batch[name] !== '') {
          v[name] = this.batch[name]
        } else if (type === 2 && reg2.test(this.batch[name])) {
          v[name] = this.batch[name]
        } else if (type === 3 && reg3.test(this.batch[name])) {
          v[name] = this.batch[name]
        } else {
          this.$errorMessage('')
        }
      })
    },
    addCustomStandardItem(index) {
      // 
      this.$refs['dialogForm'].validate(valid => {
        if (valid) {
          customStandardItem({
            attrGroupName: '',
            name: this.popoverForm.name,
            attrValues: this.popoverForm.attrValues,
            typeId: this.productTypeId,
            storeId: this.storeId,
            valueType: 2
          }).then(res => {
            this.saleStandardInfos.push(res.data)
            // 
            this.properties.push({
              valueType: res.data.valueType,
              attrGroupName: res.data.attrGroupName,
              name: res.data.name,
              id: res.data.id,
              attrValueList: []
            })
            this.popoverVisible = false
            this.popoverForm.name = ''
            this.popoverForm.attrValues = ''
          })
        } else {
          return false
        }
      })
    },
    clearCustomStandardItem() {
      this.popoverVisible = false
      this.popoverForm.name = ''
      this.popoverForm.attrValues = ''
      this.$refs.dialogForm.resetFields()
    },
    addCustomStandard(index) { // 
      if (this.customStandardValue['value' + index] === undefined) { // 
        this.$errorMessage('')
      } else {
        const params = {
          attrValue: this.customStandardValue['value' + index],
          storeId: this.storeId,
          attrId: this.saleStandardInfos[index].id
        }
        customStandard(params).then(res => {
          if (res.code === 200) {
            const obj = {
              attrCode: res.data.attrCode,
              attrValue: res.data.attrValue,
              id: res.data.id,
              attrType: res.data.attrType,
              valueResourceId: ''
            }
            this.saleStandardInfos[index].attrValueList.push(obj)
            this.customStandardValue['value' + index] = ''
          }
        })
      }
    },
    addCustomAttribute(i) {
      var index = i
      if (this.customAttributeValue['value' + index] !== undefined) {
        // 
        const params = {
          name: this.customAttributeValue['value' + index],
          attrGroupName: this.productStandardInfos[index].attrGroupName,
          attrValues: 'no',
          typeId: this.productTypeId,
          storeId: this.storeId
        }
        customAttribute(params).then(res => {
          if (res.code === 200) {
            const obj = {
              attrGroupName: res.data.attrGroupName,
              attrValueList: res.data.attrValueList,
              custom: res.data.custom,
              id: res.data.id,
              name: res.data.name,
              valueType: res.data.valueType,
              attrType: res.data.attrType
            }
            this.productStandardInfos[index].attrs.push(obj)
            this.customAttributeValue['value' + index] = ''
          }
        })
      } else {
        this.$errorMessage('')
      }
    },
    validateRequire() { // 
      let flag = true
      if (!this.formData.coreModel.hasSku || this.saleStandardInfos.length === 0) {
        flag = true
      } else { // 
        this.saleStandardInfos.forEach((v, i) => { // skuTypeAttrs(properties)attrValueList
          if (v.required) {
            var item = this.properties[i]
            if (item.attrValueList.length <= 0) {
              this.$errorMessage('' + item.name)
              flag = false
              return
            }
          }
        })
      }
      return flag
    },
    validateAttrRequired() { // 
    // todo  
      let flag = true
      const ids = []// id
      if (this.productStandardInfos.length > 0) {
        this.productStandardInfos.forEach(v => {
          v.attrs.forEach(vv => {
            if (vv.required) {
              ids.push(vv.id)
              if (this.attributes.length < ids.length) {
                flag = false
                this.$errorMessage('')
              }
              if (this.attributes.length > 0) { // 
                for (let i = 0; i < this.attributes.length; i++) {
                  if (!this.attributes[i].attrVc) {
                    flag = false
                    this.$errorMessage('')
                    break
                  }
                }
              }
            }
          })
        })
      }
      return flag
    },
    isAttrRequired(obj) { // 
      if (obj.required) {
        return true
      }
    },
    deleteCustomStandard(item, index) { // 
      productStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          this.saleStandardInfos.splice(index, 1)
          this.properties.splice(index, 1)
        }
      })
    },
    deleteCustomStandardValue(item, parentIndex, index) {
      // 
      console.log('parentIndex' + parentIndex)
      console.log('index' + index)
      customStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          const $index = this.checkSaleStandardIds.findIndex(
            v => v.id === item.id
          )
          this.checkSaleStandardIds.splice($index, 1)
          this.setProperties(this.checkSaleStandardIds)
          this.saleStandardInfos[parentIndex].attrValueList.splice(index, 1)
        }
      })
    },
    deleteCustomAttribute(item, parentIndex, index) {
      // 
      productStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          this.productStandardInfos[parentIndex].attrs.splice(index, 1)
        }
      })
    },
    getStandardImg(items, index, params) {
      // 
      this.$set(
        // id
        this.saleStandardInfos[params.parentIndex].attrValueList[index],
        'valueResourceId',
        items[0].id
      )
      this.$set(
        // 
        this.saleStandardInfos[params.parentIndex].attrValueList[index],
        'url',
        items[0].url
      )
    },
    prevStep() {
      this.$store.dispatch('setCache', true)
      this.$store.dispatch('setStep', 'step2')
    },
    save() {
      // 
      this.$refs.form.validate((valid, item) => {
        if (valid && this.validateRequire() && this.validateSkus() && this.validateAttrRequired()) { // sku
          const productInfos = this.$cms.deepCopy(this.formData)
          productInfos.skuTypeAttrs = this.properties
          productInfos.skus = this.skus
          productInfos.proTypeAttrs = this.attributes
          this.setCoreModel(this.formData.coreModel.hasSku)// sku
          this.$store.dispatch('setStandardInfo', productInfos).then(param => {
            this.loading = true
            productSave(param).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$successMessage('!')
                setTimeout(() => {
                  this.$routerLink('/product/sale', 'list')
                }, 1000)
              } else {
                this.$errorMessage(res.message)
              }
            })
          })
        } else {
          // 
          let name = ''
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              name = key.replace('.', '_')
              break
            }
          }
          if (name !== '') {
            this.errorPosition(name)
          }
        }
      })
    },
    errorPosition(name) {
      // 
      const lay = document.querySelector('#layout')
      const ele = this.$refs[name].$el
      lay.scrollTop = ele.offsetTop - 350
      return false
    },
    restData() {
      // 
      this.formData = this.$cms.deepCopy(defaultData)
      this.customStandardValue = {} // 
      this.customAttributeValue = {} // 
      this.checkSaleStandardIds = [] // id
      this.checkProductStandardIds = [] // id
      this.properties = [] // 
      this.attributes = [] // 
      this.saleStandardInfos = [] // 
      this.productStandardInfos = [] // 
      this.skus = [] // sku
      this.attributeGroups = {}
    },
    validateSkus() { // sku
      let flag = true
      if (this.formData.coreModel.hasSku) { // 
        const reg1 = /^\d{0,8}\.{0,1}(\d{1,2})?$/// 
        const reg2 = /^[0-9]{1}[0-9]*$/// 
        const reg3 = /^[0-9]+$/// 
        if (this.skus.length > 0) {
          this.skus.forEach(item => { // ,
            if (!reg1.test(item.salesPrice) || !reg1.test(item.costPrice) || !reg1.test(item.marketPrice)) {
              flag = false
              this.$errorMessage('')
            } else if (!reg2.test(item.inventory) || !reg3.test(item.warning)) {
              flag = false
              this.$errorMessage('')
            } else if (parseFloat(item.salesPrice) <= 0) {
              flag = false
              this.$errorMessage('sku0')
            } else if (parseInt(item.warning) !== 0) { // 
              if (parseInt(item.inventory) < parseInt(item.warning)) {
                flag = false
                this.$errorMessage('sku')
              }
            }
          })
        }
      }
      return flag
    },
    handleClose() {
      this.popoverVisible = false
      this.$refs.dialogForm.resetFields()
      this.popoverForm.name = ''
      this.popoverForm.attrValues = ''
    }
  },
  watch: {
    allCheckedLength: {
      //  sku
      handler() {
        this.createdSkus()
      }
    }
  },
  activated() {
    const cacheProductTypeId = this.$store.state.product.productTypeId // id
    const cacheStoreId = this.$store.state.product.storeId // id
    if (
      cacheProductTypeId !== this.productTypeId ||
      cacheStoreId !== this.storeId
    ) {
      // 
      this.restData()
      this.getProductInfo()
    }
  },
  mounted() {
    const lay = document.querySelector('#layout')
    lay.addEventListener('scroll', e => {
      let scrollTop = e.target.scrollTop
      const clientHeight = e.target.clientHeight
      const scrollHeigh = e.target.scrollHeight
      if (this.fixed === false) {
        scrollTop += 70
      }
      if (parseInt(scrollTop) > scrollHeigh - clientHeight - 70) {
        this.fixed = false
      } else {
        this.fixed = true
      }
    })
  }
}
</script>
<style lang="scss" scoped>
.w-50 {
  width: 50%;
  display: inline-block;
  margin: 5px 0;
}
.w-100 {
  width: 100%;
  display: block;
}
.attribute-name {
  width: 120px;
  margin-right: 25px;
  font-size: 14px;
  color: #666;
  text-align: right;
}
.icon-zhankai1-copy {
  cursor: pointer;
}
.del {
  cursor: pointer;
  font-size: 14px;
  color: #a3a3a3;
  &:hover {
    color: #fe5f40;
  }
}
.el-icon-delete {
  width: 15px;
  height: 15px;
}
.goods-astrick{
  color: #f56c6c;
  position: relative;
  right: 4px;
}
</style>




