  <!---->
  <template>
  <div class="cms-form-layout loading-top product-main-warpper" v-loading="loading">
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent
     style="margin-bottom:48px;">
      <h3 class="cms-segmentation"></h3>
      <el-form-item label="" prop="product.categoryId" ref="product.categoryId">
        {{categoryName}}
      </el-form-item>
      <el-form-item label="" prop="product.storeCategoryId">
        <el-cascader :options="options" :props="props" :change-on-select='true' v-model="formData.storeCategoryIds" placeholder="">
        </el-cascader>
      </el-form-item>
      <el-form-item label="" prop="product.name" ref="product.name">
        <el-input v-model="formData.product.name" type="textarea" resize="none" spellcheck="false" maxlength="100"></el-input>
        <span class="gray">100</span>
      </el-form-item>
      <!--  -->
      <el-form-item label="" prop="product.virtualCardTplId" v-if="isVirtual">
        <el-select v-model="formData.product.virtualCardTplId" :popper-append-to-body='false'>
          <el-select v-model="formData.product.templeteId" :popper-append-to-body='false'>
            <el-option label="" :value="null"></el-option>
          </el-select>
        </el-select>
        <span class="gray"></span>
      </el-form-item>
      <el-form-item label="" prop="product.itemNumber" ref="product.itemNumber">
        <el-input v-model="formData.product.itemNumber" maxlength="30"></el-input>
        <span class="gray">30</span>
      </el-form-item>
      <el-form-item label="" prop="product.brandId">
        <cms-brand v-model="formData.product.brandId">
          <cms-button type='info' size='small'></cms-button>
        </cms-brand>
      </el-form-item>
      <el-form-item label="" prop="product.summary" class="product-profile">
        <el-input type='textarea' :rows='4' v-model="formData.product.summary" maxlength="100"></el-input>
        <el-color-picker v-model="formData.product.summaryColor" class="product-color-picker"></el-color-picker>
        <span class="gray">,100
        </span>
      </el-form-item>
      <el-form-item label="" prop="product.templeteId" ref="product.templeteId" class="add-select-padd">
        <el-select v-model="formData.product.templeteId" :popper-append-to-body='false' @change="changeTemplate" >
          <el-option :label="item.expressName" :value="item.id" v-for="item in freightItems" :key="item.id"></el-option>
        </el-select>
        <div>
          <table class="sku-table" style='width:380px;margin-top:10px;'>
            <tr>
              <th></th>
              <th></th>
              <th>()</th>
              <th></th>
              <th>()</th>
              <th></th>
            </tr>
            <tbody v-for="(items,index) in freightInfo.tplDetails" :key="index" 
             :style="{'background-color':freightInfo.isFree?'#5CB85C':'#CCCCCC'}">
              <tr v-for="(freight,indexs) in items.tplDetails" :key="indexs">
                <td :rowspan="items.tplDetails.length" v-if="indexs==0">{{items.deliveryWay|deliveryWayFilter}}</td>
                <td>{{freight.districtidList|districtFilter}}</td>
                <td>{{freight.firstNum}}</td>
                <td>{{$cms.float(freight.firstPrice)}}</td>
                <td>{{freight.addNum}}</td>
                <td>{{$cms.float(freight.addPrice)}}</td>
              </tr>
            </tbody>
            <tr style="background-color:#5CB85C" v-if="freightInfo.isFree">
              <td colspan="6"></td>
            </tr>
          </table>
        </div>
      </el-form-item>
      <el-form-item label="" prop="product.volume" ref='product.volume'>
        <el-input v-model="formData.product.volume" maxlength="8">
          <template slot="append"></template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="product.weight" ref='product.weight'>
        <el-input v-model="formData.product.weight" maxlength="8">
          <template slot="append">kg</template>
        </el-input>
      </el-form-item>
      <h3 class="cms-segmentation"></h3>
      <el-form-item label="" prop="checked">
        <cms-picture-zone type='video' viewType='media' @change="getProductVideo">
          <cms-button type='info' size='small'></cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="" prop="imageResources" ref='imageResources' :error='errorMessage.imageResources' class="imageResource">
        <div class="clearfix">
          <!--  -->
          <draggable v-model="formData.imageResources" :options="{animation:150}">
            <cms-picture-zone class="product-img-box" v-for="(item,index) in formData.imageResources" :key="index" :preview='false' :index='index' @change="getProductImg">
              <div class="product-img-warpper">
                <img v-if="formData.imageResources[index]&&formData.imageResources[index].url!==''" :src="$cms.filterUrl(formData.imageResources[index].url)" alt="">
                <span v-else class="img-plus el-icon-plus"></span>
              </div>
              <!--  -->
              <div class="img-move-bar" @click.stop>
                <span class="el-icon-arrow-left next" :class="[index===0?'disable':'']" @click.stop="imgMove(index,'next')"></span>
                <span class="el-icon-arrow-right next" :class="[index===4?'disable':'']" @click.stop="imgMove(index,'prev')"></span>
                <span class="el-icon-close delete" @click.stop="imgDelete(index)"></span>
              </div>
            </cms-picture-zone>
          </draggable>
        </div>
        <span class="gray" style="max-width:490px;">
          7
        </span>
      </el-form-item>
      <h3 class="cms-segmentation"></h3>
      <el-form-item label="" prop="checked" v-show="txtModel=='pc'" label-width="0">
        <cms-editor v-model="formData.txt.pcTxt"></cms-editor>
      </el-form-item>
      <el-form-item label="" prop="txt.topTplId" v-show="txtModel=='pc'" class="add-select-padd">
        <el-select v-model="formData.txt.topTplId" :popper-append-to-body='false'>
          <el-option v-for="item in formatItems" :label="item.name" :value="item.id" :key="item.id">
          </el-option>
        </el-select>
        <span class="gray">
          PC
        </span>
      </el-form-item>
      <el-form-item label="" prop="txt.bottomTplId" v-show="txtModel=='pc'" class="add-select-padd">
        <el-select v-model="formData.txt.bottomTplId" :popper-append-to-body='false'>
          <el-option v-for="item in formatItems" :label="item.name" :value="item.id" :key="item.id">
          </el-option>
        </el-select>
        <span class="gray">
          PC
        </span>
      </el-form-item>
      <h3 class="cms-segmentation"></h3>
      <el-form-item label="" prop="ext.unit">
        <el-input v-model="formData.ext.unit" maxlength="5"></el-input>
        <span class="gray">
        </span>
      </el-form-item>
       <el-form-item label="" prop="ext.storeServiceIds">
        <el-checkbox-group v-model="formData.ext.storeServiceIds">
          <el-checkbox v-for="item in serviceItems" :key="item.id" :label="item.id">{{item.serviceName}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="" prop="product.sellGoodsSale" ref="product.sellGoodsSale">
        <cms-switch v-model="formData.product.sellGoodsSale"></cms-switch>
      </el-form-item>
      <el-form-item label="" prop="product.isInvoice" ref="product.isInvoice">
        <cms-switch v-model="formData.product.isInvoice"></cms-switch>
      </el-form-item>
      <el-form-item label="" prop="ext.keyword">
        <el-input v-model="formData.ext.keyword" placeholder=""
         type="textarea"  resize="none" maxlength="200">
        </el-input>
        <span class="gray">,,5','</span>
      </el-form-item>
      <el-form-item label="" prop="ext.evaluationView">
        <el-input v-model="formData.ext.evaluationView" placeholder=""
         type="textarea"  resize="none" maxlength="200">
        </el-input>
        <span class="gray">','</span>
      </el-form-item>
      <el-form-item label="" prop="roleName">
        <el-radio-group v-model="formData.product.status">
          <el-radio :label="3"></el-radio>
          <el-radio :label="0"></el-radio>
        </el-radio-group>
        <span class="gray"> 
        </span>
      </el-form-item>
    </el-form>
    <div class="product-footer" :class="[fixed?'fixed-footer':'']">
      <cms-button style="background-color:#f7f7f7;color:#444444" @click="prevStep"></cms-button>
      <cms-button v-color @click="nextStep" style="background:#efae00">,/</cms-button>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import rules from '@/rules'
import { storeCategoryTree, storeServiceList } from '@/api/shop/shopManage.js' // 
import { ckItemNumber, productFormatList } from '@/api/product/product.js'
import { productCategoryGet } from '@/api/product/category.js'
import { freightList, freightGet } from '@/api/shop/freight.js'
import draggable from 'vuedraggable'
var defaultData = {
  product: {
    storeId: '',
    storeCategoryId: null,
    categoryId: '',
    name: '',
    virtualCardTplId: null,
    itemNumber: '',
    brandId: '',
    summary: '',
    summaryColor: '#ff0000',
    templeteId: '',
    weight: 0,
    volume: 0,
    sellGoodsSale: true,
    isInvoice: true,
    status: 3
  },
  productTypeId: '',
  storeCategoryIds: [],
  imageResources: [
    // 
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    }
  ],
  videoResources: [
    {
      remark: '',
      vdResourceId: '',
      url: ''
    }
  ],
  txt: {
    pcTxt: '',
    mobileTxt: '',
    topTplId: null,
    bottomTplId: null
  },
  ext: {
    maxDeductible: 0,
    unit: '',
    keyword: '',
    evaluation: '',
    storeServiceId: '',
    storeServiceIds: []
  }
}
export default {
  components: { draggable },
  data() {
    var checkCode = (rule, value, callback) => { // 
      if (value === '') {
        callback()
      } else {
        ckItemNumber({
          itemNumber: value
        }).then(res => {
          if (res.data) {
            callback()
          } else {
            callback(new Error(''))
          }
        })
      }
    }
    return {
      loading: false,
      status: false,
      fixed: true,
      txtModel: 'pc',
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },
      freightItems: [], // 
      freightInfo: {}, // 
      serviceItems: [],
      formatItems: [], // 
      formData: Object.assign({}, defaultData),
      categoryName: '',
      isVirtual: false, // 
      rules: {
        'product.templeteId': [
          rules.required('', 'change')
        ],
        'product.categoryId': [rules.required('')],
        'product.itemNumber': [rules.custom(checkCode)],
        'product.name': [
          rules.required(''),
          rules.max(100, '100')
        ],
        'product.summary': [rules.max(100, '100')],
        'product.volume': [rules.required(), rules.float(), rules.decimal()],
        'product.weight': [rules.required(), rules.float(), rules.decimal()],
        'ext.maxDeductible': [rules.float()],
        'imageResources': [rules.required('')],
        'product.sellGoodsSale': [rules.required()],
        'product.isInvoice': [rules.required()]
      },
      errorMessage: {}
    }
  },
  methods: {
    initData() {
      // 
      this.formData.product.categoryId = this.$store.state.product.categoryId // id
      this.categoryName = this.$store.state.product.categoryName // 
      this.getRelevantInfos(this.formData.product.categoryId) // 
    },
    getRelevantInfos(categoryId) {
      // 
      axios
        .all([
          productCategoryGet({ id: categoryId }),
          productFormatList(),
          freightList(),
          storeServiceList(),
          storeCategoryTree()
        ])
        .then(
          axios.spread(
            (productCategory, formatItems, freightItems, serviceItems, storeCategory) => {
              this.formatItems = formatItems.data// 
              this.formData.productTypeId = productCategory.data.productTypeId // id
              this.isVirtual = productCategory.data.virtual // 
              this.freightItems = freightItems.data// 
              this.serviceItems = serviceItems.data
              this.options = []
              const root = []
              this.options = root.concat(storeCategory.data)
              this.loading = false
            }
          )
        )
    },
    changeTemplate(id) { // 
      freightGet({ id: id }).then(res => { // 
        this.freightInfo = res.data
      })
    },
    getStoreCategoryId(value) {
      this.formData.product.storeCategoryId = value[value.length - 1]
    },
    validateImgs() {
      // 
      let state = false
      this.formData.imageResources
      for (const v of this.formData.imageResources) {
        if (v.imgResourceId !== '') {
          state = true
        }
      }
      if (state) {
        return true
      } else {
        this.errorMessage = {}
        setTimeout(() => {
          this.$set(
            this.errorMessage,
            'imageResources',
            ''
          )
          this.errorPosition('imageResources')
        }, 1)
        return false
      }
    },
    prevStep() {
      // 
      this.$store.dispatch('setCache', true)
      this.$store.dispatch('setStep', 'step1')
    },
    nextStep() {
      // 
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          if (this.validateImgs()) {
            // 
            const obj = this.formatFormData(this.formData)
            this.$store.dispatch('setBaseProductInfo', obj).then(res => {
              this.$store.dispatch('setStep', 'step3')
            })
          }
        } else {
          // 
          let name = ''
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              name = key
              break
            }
          }
          this.errorPosition(name)
        }
      })
    },
    formatFormData(data) {
      // 
      const obj = Object.assign({}, data)
      obj.ext.evaluation = obj.ext.evaluationView
      if (obj.ext.storeServiceIds.length > 0) {
        obj.ext.storeServiceId = obj.ext.storeServiceIds.join(',')
      }
      // 
      obj.product.storeCategoryId =
        data.storeCategoryIds[data.storeCategoryIds.length - 1]
      return obj
    },
    errorPosition(name) {
      // 
      const lay = document.querySelector('html')
      const ele = this.$refs[name].$el
      lay.scrollTop = ele.offsetTop - 350
      return false
    },
    getProductVideo(item, index) {
      if (item) { // 
        if (this.formData.videoResources.length > 0) {
          this.$set(this.formData.videoResources, index, {
            remark: '',
            vdResourceId: item[0].id,
            url: item[0].url
          })
        }
      } else { // 
        this.formData.videoResources = [
          {
            remark: '',
            vdResourceId: '',
            url: ''
          }
        ]
      }
    },
    getProductImg(item, index) {
      // 
      this.$set(this.formData.imageResources, index, {
        remark: '',
        imgResourceId: item[0].id,
        url: item[0].url
      })
      this.errorMessage = {}
    },
    imgMove(index, status) {
      // 
      if (status === 'next') {
        if (index === 0) {
          // 
          return false
        } else {
          const tmpItems = this.formData.imageResources[index - 1]
          this.$set(
            this.formData.imageResources,
            index - 1,
            this.formData.imageResources[index]
          )
          this.$set(this.formData.imageResources, index, tmpItems)
        }
      } else {
        if (index === 4) {
          // 
          return false
        } else {
          const tmpItems = this.formData.imageResources[index + 1]
          this.$set(
            this.formData.imageResources,
            index + 1,
            this.formData.imageResources[index]
          )
          this.$set(this.formData.imageResources, index, tmpItems)
        }
      }
    },
    imgDelete(index) {
      // 
      this.$set(this.formData.imageResources, index, {
        remark: '',
        imgResourceId: '',
        url: ''
      })
    }
  },
  filters: {
    deliveryWayFilter(way) { // 
      const waysArr = ['', '', 'ems', '']
      return waysArr[parseInt(way)]
    },
    districtFilter(list) { // 
      if (list === '') {
        return ''
      } else {
        const obj = JSON.parse(list)
        const arr = []// 
        for (const val of Object.values(obj)) {
          arr.push(val)
        }
        return arr[0] + '...'// 
      }
    }
  },
  activated() {
    if (!this.$store.state.product.cache) {
      // 
      this.formData = this.$cms.deepCopy(defaultData)
    }
    this.initData() // 
  },
  mounted() {
    const lay = document.querySelector('#layout')
    lay.addEventListener('scroll', e => {
      let scrollTop = e.target.scrollTop
      const clientHeight = e.target.clientHeight
      const scrollHeigh = e.target.scrollHeight
      if (this.fixed === false) {
        scrollTop += 70
      }
      if (parseInt(scrollTop) > scrollHeigh - clientHeight - 70) {
        this.fixed = false
      } else {
        this.fixed = true
      }
    })
  }
}
</script>
<style lang="scss" scoped>
</style>




